<template>
<!--  -->
    <el-row class="header" type="flex" justify="space-between" align="middle">
        <!--  -->
        <el-col class="left" :span="6">
            <i class="el-icon-s-unfold" style="font-size:20px"></i>
            <span></span>
        </el-col>
        <!--  -->
        <el-col class="right" :span="3">
            <!--  -->
            <img :src="accountInfo.photo" alt="">
            <!--  -->
            <el-dropdown trigger="click">
                 <span>{{ accountInfo.name }}<i class="el-icon-caret-bottom"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                <el-dropdown-item ></el-dropdown-item>
                <el-dropdown-item >GIT</el-dropdown-item>
                <!-- js
                     .native  -->
                <el-dropdown-item @click.native="onLogout" ></el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </el-col>
    </el-row>
</template>

<script>
import eventBus from '../views/utils/event-bus'
export default {
  data () {
    return {
      accountInfo: {}
    }
  },
  created () {
    this.loadInfo()
    // 
    // 
    eventBus.$on('updata-user', accountInfo => {
      this.accountInfo = accountInfo
    })
  },
  methods: {
    onLogout () {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        window.localStorage.removeItem('login-token')
        this.$router.push('/login')
        this.$message({
          type: 'success',
          message: '!'
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    },
    loadInfo () {
      this.$axios({
        method: 'GET',
        url: '/user/profile'
      }).then(res => {
        // console.log(res.data)
        this.accountInfo = res.data.data
      }).catch(() => {
        this.$message.error('')
      })
    }
  }

}
</script>

<style lang="less" scoped>
  .header {
    background-color: #fff;
    height: 50px;
    .left {
      span {
        margin-left: 5px;
        vertical-align: top;
      }
    }
    .right {
      img {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        vertical-align: middle;
        margin-right: 8px;
      }
      .el-dropdown {
        cursor: pointer;
      }
    }
  }

</style>
