<template>
    <div class="login">
        <!-- elementUI card -->
        <el-card class="login-card">
            <!--   el-card  -->
            <div class="title">
                <img src="../../assets/img/logo_index.png" alt="">
            </div>
            <!--  -->
            <!-- model    rules -->
            <el-form ref="formObj" style="margin-top:25px" :model="loginForm" :rules="loginRules">
                <el-form-item prop="mobile">
                    <!--  -->
                    <!--  -->
                    <el-input placeholder="" v-model="loginForm.mobile"></el-input>
                </el-form-item>
                <el-form-item prop="code">
                    <!--  -->
                    <el-input v-model="loginForm.code" style="width:240px" placeholder=""></el-input>
                    <el-button plain style="float:right;width:140px"></el-button>
                </el-form-item>
                <el-form-item prop="checked">
                    <!--  -->
                    <el-checkbox v-model="loginForm.checked"><el-link type="primary"></el-link><el-link type="primary"></el-link></el-checkbox>
                </el-form-item>
                <el-form-item>
                    <el-button @click="login" type="primary" style="width:100%"></el-button>
                </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      loginForm: {
        mobile: '',
        code: '',
        checked: ''
      },
      //   
      loginRules: {
        mobile: [
          { required: true, message: '' },
          { pattern: /^1[3456789]\d{9}$/, message: '' }
        ],
        code: [
          { required: true, message: '' },
          { pattern: /^\d{6}$/, message: '' }
        ],
        checked: [
          // validator 
          { validator (rules, value, callback) {
            //   rules   
            // value   checked
            // callback 
            if (value) {
              // true 
              callback() //   callback
            } else {
              // false 
              callback(new Error('')) // callback  
            }
          } }
        ]
      }
    }
  },
  methods: {
    login () {
      //   this.$refs.xxx refxxxdom
    //   this.$refs.formObj el-form
    // validate()   >
      this.$refs.formObj.validate((isOk) => {
        if (isOk) {
          //     
          this.$axios.post('/authorizations', this.loginForm).then(res => {
            localStorage.setItem('login-token', res.data.data.token)
            this.$router.push('/')
          }).catch(() => {
            this.$message({
              message: '',
              type: 'warning'
            })
          })
        }
      })
    }
  }

}
</script>

// lang="less" .less
// scoped
<style lang="less" scoped>
    .login {
        height: 100vh; // 100%
        background-image: url('../../assets/img/login_bg.jpg');
        background-size: cover;
        display: flex;
        justify-content: center;
        align-items: center;

        .login-card {
            width: 440px;
            height: 360px;
            .title {
                text-align: center;
                img {
                    height: 45px;
                }
            }
        }
    }
</style>
