<template>
  <div class="comment">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span></span>
      </div>
      <div>
        <!-- el-table 
            data 
             data   v-for -->
        <el-table :data="comments" style="width: 100%">
          <!-- el-table-column 
              prop  
              label  
              width -->
          <!--   
               -->
          <el-table-column prop="title" label="" width="600px">
            <!--   template slot-scope='scope'
            scope.row 
            scope.row v-for item -->
            <!-- <template slot-scope="scope" >
              <img width="60" :src="scope.row.cover.images[0]">
            </template> -->
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
                <el-switch
                    v-model="scope.row.comment_status"
                    active-color="#13ce66"
                    inactive-color="#ff4949"
                    @change="onStatusChange(scope.row)">
                </el-switch>
            </template>
          </el-table-column>
          <el-table-column prop="total_comment_count" label="">
            <!-- <template slot-scope="scope">
              <el-tag :type="items[scope.row.status].type"> {{items[scope.row.status].label}} </el-tag>
            </template> -->
          </el-table-column>
          <el-table-column prop="fans_comment_count" label="">
          </el-table-column>
          <el-table-column label="">
                <!-- slot-scope="scope" -->
            <template slot-scope="scope">
            <el-link
              icon="el-icon-edit"
              :underline="false"
              @click="$router.push(`/comment/${scope.row.id}`)"
            ></el-link>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <!--  -->
      <div style="margin-top:20px;margin-left:400px">
        <el-pagination
            background
            layout="prev, pager, next"
            :total="total_count"
            @current-change="onCurrentPage">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'CommentArticle',
  data () {
    return {
      comments: [],
      total_count: 0,
      page: 1
    }
  },
  created () {
    this.loadComment(1)
  },
  methods: {
    loadComment (page) {
      this.$axios({
        methos: 'GET',
        url: '/articles',
        params: {
          page,
          response_type: 'comment'
        }
      }).then(res => {
        console.log(res.data)
        this.comments = res.data.data.results
        this.total_count = res.data.data.total_count
      })
    },

    // 
    onStatusChange (data) {
      this.$axios({
        method: 'PUT',
        url: '/comments/status',
        params: {
          article_id: data.id.toString()
        },
        data: {
          allow_comment: data.comment_status
        }
      }).then(res => {
        console.log(res.data)
        this.$message({
          type: 'success',
          message: `${data.comment_status ? '' : ''}`
        })
      })
    },
    // 
    onCurrentPage (page) {
      this.page = page
      this.loadComment(page)
    }
  }

}
</script>

