<template>
  <div class="article">
    <!--  -->
    <el-card class="box-card">
      <div slot="header" class="clearfix">
      <span></span>
      </div>
      <div class="text item">
        <el-form>
          <el-form-item label="">
            <el-radio-group v-model="formData.status">
              <el-radio :label="null"></el-radio>
              <el-radio label="0"></el-radio>
              <el-radio label="1"></el-radio>
              <el-radio label="2"></el-radio>
              <el-radio label="3"></el-radio>
              <!-- <el-radio label="4"></el-radio> -->
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" >
            <!-- <el-select v-model="formData.channel_id" placeholder=""> -->
              <!--  -->
              <!-- <el-option label="" :value="null"></el-option> -->
              <!--  -->
              <!-- <el-option
                v-for="channel in channels"
                :key="channel.id"
                :label="channel.name"
                :value="channel.id"
                ></el-option> -->
              <!-- <el-option label="ios" value="ios"></el-option> -->
            <!-- </el-select> -->
            <ChannelSelect
              v-model="formData.channel_id"></ChannelSelect>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker
              v-model="rangeDater"
              type="daterange"
              range-separator="-"
              start-placeholder=""
              end-placeholder=""
              value-format="yyyy-MM-dd">
              <!-- value-format -->
            </el-date-picker>
          </el-form-item>
          <el-form-item>
            <el-button
              style="margin-left:80px"
              type="primary"
              round
              @click="onQuery"
              ></el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <!--  -->
    <el-card class="box-card">
      <div slot="header" class="clearfix">
      <span>{{ num }}</span>
      </div>
      <div>
        <!-- el-table 
            data 
             data   v-for -->
        <el-table
          v-loading="loading"
          element-loading-text=""
          element-loading-spinner="el-icon-loading"
          element-loading-background="rgba(0, 0, 0, 0.8)"
          :data="articles" style="width: 100%">
          <!-- el-table-column 
              prop  
              label  
              width -->
          <!--   
               -->
          <el-table-column prop="img" label="">
            <!--   template slot-scope='scope'
            scope.row 
            scope.row v-for item -->
            <template slot-scope="scope" >
              <img width="60" :src="scope.row.cover.images[0]">
            </template>
          </el-table-column>
          <el-table-column prop="title" label="">
          </el-table-column>
          <el-table-column prop="status" label="">
            <template slot-scope="scope">
              <el-tag :type="items[scope.row.status].type"> {{items[scope.row.status].label}} </el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="pubdate" label="">
          </el-table-column>
          <el-table-column label="">
            <template  slot-scope="scope">
            <el-link icon="el-icon-edit" :underline="false" @click="$router.push(`/publish/${scope.row.id}`)"></el-link>&nbsp;
            <el-link icon="el-icon-delete" :underline="false" @click="onDelete(scope.row.id)"></el-link>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!--  -->
      <el-pagination
        style="margin-top:20px;margin-left:360px"
        background
        layout="prev, pager, next"
        :total="num"
        @current-change="onPageChange"
        :disabled="loading"
        :current-page="page"
        >
        <!-- :disabled="loading"   -->
      </el-pagination>

    </el-card>

  </div>
</template>

<script>
import ChannelSelect from '../../components/channel-select'
export default {
  name: 'article-list',
  components: {
    ChannelSelect
  },
  data () {
    return {
      formData: {
        status: null
        // channel_id: null
        // begin_pubdate: '',
        // end_pubdate: ''
      },
      rangeDater: [], // 
      tableData: [
        {
          img: '',
          title: '1',
          status: '2',
          pubdate: '3'
        }
      ],
      articles: [],
      num: 0,
      items: [
        { type: 'danger', label: '' },
        { type: '', label: '' },
        { type: 'success', label: '' },
        { type: 'warning', label: '' },
        { type: 'info', label: '' }
      ],
      loading: true, // load
      // channels: [], // 
      page: 1
    }
  },
  created () {
    // 
    this.loadArticle(1)
    // 
    // this.loadChannels()
  },
  methods: {
    loadArticle (page = 1) {
      // loading
      this.loading = true
      // const token = window.localStorage.getItem('login-token')
      this.$axios({
        method: 'GET',
        url: '/articles',
        // 
        // headers: {
        //   // 
        //   //  token  Authorization
        //   // tokenBearer token
        //   // Bearer 
        //   Authorization: `Bearer ${token}`
        // },
        params: {
          // 
          status: this.formData.status,
          page,
          pre_page: 10,
          // id
          channel_id: this.formData.channel_id,
          // 
          begin_pubdate: this.rangeDater ? this.rangeDater[0] : null, // 
          end_pubdate: this.rangeDater ? this.rangeDater[1] : null // 
        }
      }).then(res => {
        // console.log(res.data)
        this.articles = res.data.data.results
        this.num = res.data.data.total_count
      }).catch(err => {
        console.log(err, '')
      }).finally(() => {
        this.loading = false
      })
    },

    // 
    // loadChannels () {
    //   this.$axios({
    //     method: 'GET',
    //     url: '/channels'
    //   }).then(res => {
    //     // console.log(res.data)

    //     this.channels = res.data.data.channels
    //   })
    // },
    // 
    onPageChange (page) {
      // console.log(page)
      this.loadArticle(page)
      this.page = page
    },
    // 
    onDelete (id) {
      this.$axios({
        method: 'DELETE',
        url: `/articles/${id}`
        // headers: {
        //   Authorization: `Bearer ${window.localStorage.getItem('login-token')}`
        // }
      }).then(res => {
        // console.log(res.data)
        // 
        this.loadArticle(this.page)
        this.$message({
          type: 'success',
          message: ''
        })
      }).catch(() => {
        this.$message.error('')
      })
    },
    // 
    onQuery () {
      this.loadArticle(1)
      this.page = 1
    }
  }

}
</script>

<style>
.text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 100%;
    margin-bottom: 10px;
  }

</style>
