<template>
  <div class="commentDetail">
    <el-card class="box-card">
        <div slot="header" class="clearfix">
            <el-button style="float: left; padding: 3px 0" type="text"></el-button>
        </div>
        <div>
            <el-table :data="commentDetail" border style="width: 100%;">
                <el-table-column
                    label=""
                    width="180">
                    <template slot-scope="scope">
                        <img height=50 :src="scope.row.aut_photo" alt="">
                    </template>
                </el-table-column>
                <el-table-column prop="content" label="">
                </el-table-column>
                <el-table-column prop="like_count" label="">
                </el-table-column>
                <el-table-column prop="reply_count" label="">
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <!-- {{ scope.row.is_liking === 1 ? '' : '' }} -->
                        <!-- <el-switch
                            v-model='scope.row.is_liking'
                            active-color="#13ce66"
                            inactive-color="#ff4949">
                        </el-switch> -->
                        <el-link
                          icon="el-icon-edit"
                          @click="onIsLiking(scope.row)"
                        >
                        {{ scope.row.is_liking === 1 ? '' : '' }}
                        </el-link>
                    </template>
                </el-table-column>
                <el-table-column prop="is_top" label="">
                    <template slot-scope="scope">
                        <!-- {{ scope.row.is_top === 1 ? '' : '' }} -->
                        <el-switch
                            v-model='scope.row.is_top'
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            @change="onTop(scope.row)">
                        </el-switch>
                    </template>
                </el-table-column>
                <el-table-column prop="pubdate" label="">
                    <template slot-scope="scope">
                        {{ scope.row.pubdate | dateFormat }}
                    </template>
                </el-table-column>
            </el-table>
        </div>
    </el-card>

  </div>
</template>

<script>
// import moment from 'moment'
export default {
  name: 'commentDetail',
  data () {
    return {
      commentDetail: []
    }
  },
  //  this.$route.params.id 
  //  props 
  //  data 
  props: {
    id: {
      type: String,
      required: true
    }
  },
  created () {
    this.loadCommentDetail()
  },
  // 
  //   
  // 
  // 
  // {{  |  }}
  //   filters: {
  //     dateFormat (value) {
  //       return moment(value).format('YYYY-MM-DD hh:mm:ss')
  //     }
  //   },
  methods: {
    loadCommentDetail () {
      this.$axios({
        method: 'GET',
        url: '/comments',
        params: {
          type: 'a',
          //  this.$route.params.id
          //   source: this.$route.params.id
          source: this.id
        },
        data: {
          allow_comment: true
        }
      }).then(res => {
        console.log(res.data)
        const commentDetail = res.data.data.results
        // // moment 
        // // 
        // commentDetail.forEach(function (item) {
        //   item.pubdate = moment(item.pubdate).format('YYYY-MM-DD hh:mm:ss')
        // })
        this.commentDetail = commentDetail
      }).catch(err => {
        console.log(err)
        this.$message.error('')
      })
    },
    onTop (item) {
      this.$axios({
        method: 'PUT',
        url: `/comments/${item.com_id}/sticky`,
        data: {
          sticky: item.is_top
        }
      }).then(res => {
        // console.log(res.data)
        if (res.data.data.sticky) {
          this.$message({
            type: 'success',
            message: ''
          })
        } else {
          this.$message({
            type: 'success',
            message: ''
          })
        }
      }).catch(err => {
        console.log(err)
        this.$message.error('')
      })
    },
    onIsLiking (item) {
      if (item.is_liking === 1) {
        this.$axios({
          method: 'DELETE',
          url: `/comment/likings/${item.com_id}`
        }).then(res => {
          console.log(res.data)
          item.is_liking = 0
          this.loadCommentDetail()
          this.$message({
            type: 'success',
            message: ''
          })
        }).catch(err => {
          console.log(err)
          this.$message.error('')
        })
      } else if (item.is_liking === 0) {
        this.$axios({
          method: 'POST',
          url: '/comment/likings',
          data: {
            target: item.com_id
          }
        }).then(res => {
          console.log(res.data)
          item.com_id = 1
          this.loadCommentDetail()
          this.$message({
            type: 'success',
            message: ''
          })
        }).catch(err => {
          console.log(err)
          this.$message.error('')
        })
      }
    }
  }

}
</script>

